.modal-window__locations, .modal-window__franchise {
  width: calc(100% - 40px);
  background-color: var(--txt-color-btnwhi);
  border: 2px solid var(--button-color-yel);
  border-radius: 10px;
  padding: 28px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-window__close-btn {
  fill: var(--txt-color-blck);
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-14px, 14px);
}

.modal-window__close-icon {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

