    width: 20px;
    height: 20px;
    transform: translate(-20px, 20px);
  }
}

@media screen and (min-width: 768px) {
  .modal-window__locations, .modal-window__franchise {
    height: 458px;
    width: 568px;
    padding: 32px 60px;
  }

  .modal-window__franchise {
